		},
		toFriendInfo(account) {
			console.log('toFriendInfo', account)
			uni.navigateTo({
				url: `/components/content/friend/FriendInfo?friendAccount=${account}` 
			})
		},








	},
	onLoad() {
		if(this.isGroupAdmin === 3) {



		}
	},
	onNavigationBarButtonTap(options) {
		if(options.index === 0) {
			this.changeMode()
		}
	}
}
